
.window {
  border: 1px solid rgb(195, 195, 195);
  width: 500px;
  border-radius: 4px;
  box-shadow: 0px 2px 3px 0px rgba(50, 50, 50, 0.11);
  text-align: left;
  vertical-align: top;
  margin: 0 20px;
  display: inline-block;
}
.window.browser .header {
  background: #E8E8E8;
}
.window.browser .header .title {
  background: #fff;
  display: inline-block;
  width: 320px;
  border-radius: 4px;
  padding: 6px 14px 4px;
  margin-left: 10px;
  vertical-align: middle;
}
.window .header {
  padding: 10px 0;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  position: relative;
}
.window .header .title {
  display: inline-block;
  width: 362px;
  text-align: center;
  font-size: 12px;
  color: #766F79;
}
.window .body {
  padding: 10px;
  position: relative;
  min-height: 300px;
}
.window .bullets {
  font-size: 0;
  position: absolute;
  left: 10px;
}
.window .bullet {
  height: 11px;
  width: 11px;
  display: inline-block;
  background: #ccc;
  border-radius: 100%;
  vertical-align: middle;
  margin-right: 5px;
}
.window .bullet.bullet-red {
  background: #DF7065;
}
.window .bullet.bullet-yellow {
  background: #E6BB46;
}
.window .bullet.bullet-green {
  background: #5BCC8B;
}



